import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { ThemeProvider } from './context/ThemeContext';
import { RealTimeProvider } from './context/RealTimeContext';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './styles/global.css';
import './styles/theme.css';
import './styles/swapV2.css';
import './styles/swapV2PC.css';

// Components
import Header from './components/common/Header';
import Footer from './components/common/Footer';
import HomePage from './pages/HomePage';
import ResponsiveSwapPage from './components/common/ResponsiveSwapPage';
import EditTokenPage from './pages/EditTokenPage';
import CreateTokenPage from './pages/CreateTokenPage';
import ManageTokenPage from './pages/ManageTokenPage';
import TrendingPage from './pages/TrendingPage';
import WalletPage from './pages/WalletPage';
import CommentPage from './pages/CommentPage';

const App = () => {
  return (
    <ThemeProvider>
      <RealTimeProvider>
        <Router>
          <div className="app-container">
            <Routes>
              <Route path="/" element={
                <>
                  <Header />
                  <main>
                    <HomePage />
                  </main>
                  <Footer />
                </>
              } />
              <Route path="/swap" element={<ResponsiveSwapPage />} />
              <Route path="/swap/:tokenSymbol" element={<ResponsiveSwapPage />} />
              <Route path="/edit-token" element={
                <>
                  <Header />
                  <main>
                    <EditTokenPage />
                  </main>
                  <Footer />
                </>
              } />
              <Route path="/create-token" element={
                <>
                  <Header />
                  <main>
                    <CreateTokenPage />
                  </main>
                  <Footer />
                </>
              } />
              <Route path="/manage-tokens" element={
                <>
                  <Header />
                  <main>
                    <ManageTokenPage />
                  </main>
                  <Footer />
                </>
              } />
              <Route path="/trending" element={
                <>
                  <Header />
                  <main>
                    <TrendingPage />
                  </main>
                  <Footer />
                </>
              } />
              <Route path="/wallet" element={
                <>
                  <Header />
                  <main>
                    <WalletPage />
                  </main>
                  <Footer />
                </>
              } />
              <Route path="/comment" element={
                <>
            <Header />
            <main>
                    <CommentPage />
            </main>
            <Footer />
                </>
              } />
            </Routes>
            <ToastContainer position="bottom-right" />
          </div>
        </Router>
      </RealTimeProvider>
    </ThemeProvider>
  );
};

export default App;