import React from 'react';
import './TokenHeaderDashboard.css';

const TokenHeaderDashboard = ({
  logo = 'E',
  name = 'Ethereum',
  symbol = 'ETH',
  price = '$3,420.25',
  change = '+2.75%',
  volume = '$18.2B',
  marketCap = '$410.5B',
}) => (
  <div className="token-header">
    <div className="token-logo">{logo}</div>
    <div className="token-info">
      <div className="token-name">{name}</div>
      <div className="token-symbol">{symbol}</div>
    </div>
    <div className="token-stats">
      <div className="token-stat">
        <div className="stat-label">Price</div>
        <div className="stat-value">{price}</div>
      </div>
      <div className="token-stat">
        <div className="stat-label">24h Change</div>
        <div className={`stat-value price-change ${change.startsWith('+') ? 'positive' : 'negative'}`}>{change}</div>
      </div>
      <div className="token-stat">
        <div className="stat-label">24h Volume</div>
        <div className="stat-value">{volume}</div>
      </div>
      <div className="token-stat">
        <div className="stat-label">Market Cap</div>
        <div className="stat-value">{marketCap}</div>
      </div>
    </div>
  </div>
);

export default TokenHeaderDashboard; 