import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';

const TokenCard = ({ token }) => {
  const [expanded, setExpanded] = useState(false);
  const [liked, setLiked] = useState(token.liked);
  const [likeCount, setLikeCount] = useState(token.likes);
  const [copyNotification, setCopyNotification] = useState(false);
  const navigate = useNavigate();

  const toggleDescription = () => {
    setExpanded(!expanded);
  };

  const handleLike = () => {
    if (liked) {
      setLikeCount(likeCount - 1);
    } else {
      setLikeCount(likeCount + 1);
    }
    setLiked(!liked);
  };

  const copyAddress = (address) => {
    navigator.clipboard.writeText(address);
    setCopyNotification(true);
    setTimeout(() => setCopyNotification(false), 2000);
  };

  const getStatusClass = (status) => {
    if (status === 'Migrated') return '';
    if (status === 'Migrating') return 'migrating';
    return 'new';
  };

  const handleCommentClick = () => {
    // Navigate to swap page with comment tab active
    navigate(`/swap/${token.symbol}?tab=comment`);
  };

  return (
    <div className="token-card">
      <div className="token-card-header">
        <div className="migration-status-corner">
          <span className={`migration-status ${getStatusClass(token.status)}`}>
            {token.status}
          </span>
        </div>
        <div className="token-header-main">
          <div className="token-logo">
            {token.logoUrl ? (
              <img src={token.logoUrl} alt={`${token.name} Logo`} />
            ) : (
              token.name.charAt(0)
            )}
          </div>
          <div className="token-details">
            <div className="token-name">{token.name}</div>
            <div className="token-symbol">{token.symbol}</div>
          </div>
        </div>
        <div className="social-icons">
          <div className="social-icon">
            <i className="fab fa-telegram"></i>
          </div>
          <div className="social-icon">
            <i className="fas fa-globe"></i>
          </div>
          <div className="social-icon">
            <i className="fab fa-twitter"></i>
          </div>
        </div>
      </div>
      
      <div className="token-card-body">
        <div className={`description ${expanded ? 'expanded' : ''}`}>
          {token.description}
          <span className="read-more" onClick={toggleDescription}>
            {expanded ? 'Show less' : 'Read more...'}
          </span>
        </div>
        
        <div className="stats-container">
          <div className="stat-row">
            <span className="stat-label">
              <i className="fas fa-chart-line"></i>
              Market Cap
            </span>
            <span className="stat-value">{token.marketCap}</span>
          </div>
          <div className="stat-row">
            <span className="stat-label">
              <i className="fab fa-ethereum"></i>
              ETH in Pool
            </span>
            <span className="stat-value">{token.ethInPool}</span>
          </div>
          <div className="progress-container">
            <div className="progress-header">
              <span className="progress-label">Bonding Progress</span>
              <span className="progress-value">{token.progress}%</span>
            </div>
            <div className="progress-bar">
              <div 
                className={`progress-fill ${token.progress === 100 ? 'migrated' : ''}`} 
                style={{ width: `${token.progress}%` }}
              ></div>
            </div>
          </div>
        </div>
      </div>
      
      <div className="token-card-footer">
        <div className="action-buttons">
          <Link 
            to={`/swap/${token.symbol}`} 
            className="action-btn swap-btn"
          >
            <i className="fas fa-exchange-alt"></i>
            Swap
          </Link>
          <button className="action-btn" onClick={handleCommentClick}>
            <i className="fas fa-comment"></i>
            Comment
          </button>
        </div>
        
        <div className="like-container">
          <button 
            className={`like-btn ${liked ? 'liked' : ''}`} 
            onClick={handleLike}
          >
            <i className="fas fa-heart"></i>
          </button>
          <span className="like-count">{likeCount}</span>
        </div>
        
        <div className="copy-address-container">
          <button 
            className="copy-address" 
            onClick={() => copyAddress('0x742d35Cc6634C0532925a3b844Bc454e4438f44e')}
          >
            <i className="fas fa-copy"></i>
            Token Address
          </button>
          <button 
            className="copy-address" 
            onClick={() => copyAddress('0x742d35Cc6634C0532925a3b844Bc454e4438f44e')}
          >
            <i className="fas fa-copy"></i>
            Curve Address
          </button>
        </div>
      </div>
      
      {copyNotification && (
        <div className="copy-notification show">
          Address copied to clipboard!
        </div>
      )}
    </div>
  );
};

export default TokenCard;