import React, { createContext, useContext, useEffect, useState } from 'react';
import eventService from '../services/events';

const RealTimeContext = createContext();

export const useRealTime = () => {
  const context = useContext(RealTimeContext);
  if (!context) {
    throw new Error('useRealTime must be used within a RealTimeProvider');
  }
  return context;
};

export const RealTimeProvider = ({ children }) => {
  const [connectionStatus, setConnectionStatus] = useState('disconnected');
  const [liveTokens, setLiveTokens] = useState([]);
  const [livePrices, setLivePrices] = useState({});
  const [liveSwaps, setLiveSwaps] = useState([]);
  const [liveLiquidity, setLiveLiquidity] = useState([]);
  const [recentEvents, setRecentEvents] = useState([]);

  // Initialize WebSocket connection
  useEffect(() => {
    eventService.connect();

    // Listen for connection status changes
    const unsubscribeConnection = eventService.subscribe('connection', (status) => {
      setConnectionStatus(status.status);
    });

    // Listen for token creation events
    const unsubscribeTokenEvents = eventService.subscribeToTokenEvents((data) => {
      console.log('New token created:', data);
      
      // Add to live tokens immediately
      setLiveTokens(prev => {
        const newToken = {
          id: Date.now(), // Temporary ID until saved to DB
          name: data.name,
          symbol: data.symbol,
          contractAddress: data.contractAddress,
          status: 'New',
          createdAt: data.timestamp,
          isLive: true // Flag to indicate this is a live event
        };
        
        // Add to recent events
        setRecentEvents(prev => [{
          type: 'TOKEN_CREATED',
          data: newToken,
          timestamp: new Date()
        }, ...prev.slice(0, 9)]); // Keep last 10 events
        
        return [newToken, ...prev];
      });
    });

    // Listen for migration events
    const unsubscribeMigrationEvents = eventService.subscribeToMigrationEvents((data) => {
      console.log('Token migrated:', data);
      
      // Update token status immediately
      setLiveTokens(prev => prev.map(token => 
        token.contractAddress === data.contractAddress 
          ? { ...token, status: 'Migrated', isLive: true }
          : token
      ));

      setRecentEvents(prev => [{
        type: 'TOKEN_MIGRATED',
        data,
        timestamp: new Date()
      }, ...prev.slice(0, 9)]);
    });

    // Listen for price updates
    const unsubscribePriceUpdates = eventService.subscribeToPriceUpdates((data) => {
      console.log('Price update:', data);
      
      // Update live prices immediately
      setLivePrices(prev => ({
        ...prev,
        [data.symbol]: {
          price: data.price,
          change24h: data.change24h,
          volume24h: data.volume24h,
          timestamp: data.timestamp,
          isLive: true
        }
      }));

      // Update token in live tokens if exists
      setLiveTokens(prev => prev.map(token => 
        token.symbol === data.symbol 
          ? { ...token, price: data.price, change24h: data.change24h, isLive: true }
          : token
      ));
    });

    // Listen for swap events
    const unsubscribeSwapEvents = eventService.subscribeToSwapEvents((data) => {
      console.log('Swap event:', data);
      
      // Add to live swaps immediately
      setLiveSwaps(prev => [{
        id: Date.now(),
        ...data,
        isLive: true
      }, ...prev.slice(0, 49)]); // Keep last 50 swaps

      setRecentEvents(prev => [{
        type: 'SWAP_EVENT',
        data,
        timestamp: new Date()
      }, ...prev.slice(0, 9)]);
    });

    // Listen for liquidity events
    const unsubscribeLiquidityEvents = eventService.subscribeToLiquidityEvents((data) => {
      console.log('Liquidity event:', data);
      
      // Add to live liquidity events
      setLiveLiquidity(prev => [{
        id: Date.now(),
        ...data,
        isLive: true
      }, ...prev.slice(0, 49)]); // Keep last 50 events

      setRecentEvents(prev => [{
        type: 'LIQUIDITY_EVENT',
        data,
        timestamp: new Date()
      }, ...prev.slice(0, 9)]);
    });

    // Listen for all events
    const unsubscribeAllEvents = eventService.subscribe('event', (event) => {
      console.log('General event received:', event);
    });

    // Cleanup on unmount
    return () => {
      unsubscribeConnection();
      unsubscribeTokenEvents();
      unsubscribeMigrationEvents();
      unsubscribePriceUpdates();
      unsubscribeSwapEvents();
      unsubscribeLiquidityEvents();
      unsubscribeAllEvents();
      eventService.disconnect();
    };
  }, []);

  // Get live token by symbol
  const getLiveToken = (symbol) => {
    return liveTokens.find(token => token.symbol === symbol);
  };

  // Get live price by symbol
  const getLivePrice = (symbol) => {
    return livePrices[symbol];
  };

  // Get recent events by type
  const getRecentEventsByType = (type) => {
    return recentEvents.filter(event => event.type === type);
  };

  // Get all live data
  const getLiveData = () => {
    return {
      tokens: liveTokens,
      prices: livePrices,
      swaps: liveSwaps,
      liquidity: liveLiquidity,
      events: recentEvents
    };
  };

  const value = {
    connectionStatus,
    liveTokens,
    livePrices,
    liveSwaps,
    liveLiquidity,
    recentEvents,
    getLiveToken,
    getLivePrice,
    getRecentEventsByType,
    getLiveData,
    isConnected: connectionStatus === 'connected'
  };

  return (
    <RealTimeContext.Provider value={value}>
      {children}
    </RealTimeContext.Provider>
  );
}; 