import React, { useState, useEffect, useRef } from 'react';
import { useParams, useSearchParams } from 'react-router-dom';
import TokenHeaderDashboard from '../components/common/TokenHeaderDashboard';
import TabsDashboard from '../components/common/TabsDashboard';
import '../styles/swapV2.css';
import '../components/common/TokenHeaderDashboard.css';
import '../components/common/TabsDashboard.css';

const chartTimeframes = [
  { key: '1m', label: '1M' },
  { key: '3m', label: '3M' },
  { key: '5m', label: '5M' },
  { key: '15m', label: '15M' },
  { key: '30m', label: '30M' },
  { key: '1h', label: '1H' },
  { key: '4h', label: '4H' },
  { key: '1d', label: '1D' },
  { key: '1mo', label: '1Mo' },
];

const SwapPageV2 = () => {
  const { tokenSymbol } = useParams();
  const [searchParams] = useSearchParams();
  const [fromToken, setFromToken] = useState({ symbol: 'ETH', name: 'Ethereum', logo: 'E' });
  const [toToken, setToToken] = useState({ 
    symbol: tokenSymbol || 'USDC', 
    name: tokenSymbol ? `${tokenSymbol} Token` : 'USD Coin', 
    logo: tokenSymbol ? tokenSymbol.charAt(0) : 'U' 
  });
  const [fromAmount, setFromAmount] = useState('0.1');
  const [toAmount, setToAmount] = useState(tokenSymbol ? '250.42' : '187.42');
  const [showTokenModal, setShowTokenModal] = useState(false);
  const [modalFor, setModalFor] = useState('from');
  const [slippage, setSlippage] = useState(0.5);
  const [showSlippageModal, setShowSlippageModal] = useState(false);
  const [isWalletConnected, setIsWalletConnected] = useState(false);
  const [activeTab, setActiveTab] = useState('swap');
  const [chartTimeframe, setChartTimeframe] = useState('1h');
  const [isMobile, setIsMobile] = useState(false);
  const [showMobileMenu, setShowMobileMenu] = useState(false);
  const chartContainerRef = useRef(null);
  const chartRef = useRef(null);

  // Check URL parameters for tab selection
  useEffect(() => {
    const tabParam = searchParams.get('tab');
    if (tabParam && ['swap', 'transaction', 'holders', 'comment'].includes(tabParam)) {
      setActiveTab(tabParam);
    }
  }, [searchParams]);

  // Check if mobile/tablet
  useEffect(() => {
    const checkMobile = () => {
      setIsMobile(window.innerWidth <= 1024);
    };
    checkMobile();
    window.addEventListener('resize', checkMobile);
    return () => window.removeEventListener('resize', checkMobile);
  }, []);

  // Chart logic
  useEffect(() => {
    const initChart = async () => {
      if (chartContainerRef.current && !chartRef.current) {
        const { createChart } = await import('lightweight-charts');
        chartRef.current = createChart(chartContainerRef.current, {
          width: chartContainerRef.current.clientWidth,
          height: chartContainerRef.current.clientHeight,
          layout: {
            backgroundColor: '#000',
            textColor: getComputedStyle(document.documentElement).getPropertyValue('--text-primary'),
          },
          grid: {
            vertLines: { visible: false },
            horzLines: { visible: false }
          },
          timeScale: {
            borderColor: 'rgba(255, 255, 255, 0.1)',
            timeVisible: true,
            secondsVisible: false,
          },
          crosshair: {
            mode: 1,
          },
        });
        const candlestickSeries = chartRef.current.addCandlestickSeries({
          upColor: getComputedStyle(document.documentElement).getPropertyValue('--up-color'),
          downColor: getComputedStyle(document.documentElement).getPropertyValue('--down-color'),
          borderDownColor: getComputedStyle(document.documentElement).getPropertyValue('--down-color'),
          borderUpColor: getComputedStyle(document.documentElement).getPropertyValue('--up-color'),
          wickDownColor: getComputedStyle(document.documentElement).getPropertyValue('--down-color'),
          wickUpColor: getComputedStyle(document.documentElement).getPropertyValue('--up-color'),
        });
        // Sample data
        const now = Date.now() / 1000;
        const data = [];
        let open = 1800;
        for (let i = 0; i < 100; i++) {
          const time = now - (100 - i) * 3600;
          const close = open + (Math.random() - 0.5) * 60;
          const high = Math.max(open, close) + Math.random() * 40;
          const low = Math.min(open, close) - Math.random() * 40;
          data.push({ time, open, high, low, close });
          open = close;
        }
        candlestickSeries.setData(data);
        // Resize
        const handleResize = () => {
          chartRef.current.applyOptions({
            width: chartContainerRef.current.clientWidth,
            height: chartContainerRef.current.clientHeight
          });
        };
        window.addEventListener('resize', handleResize);
        return () => window.removeEventListener('resize', handleResize);
      }
    };
    initChart();
    return () => {
      if (chartRef.current) {
        chartRef.current.remove();
        chartRef.current = null;
      }
    };
  }, []);

  // Conversion rate logic
  useEffect(() => {
    const conversionRates = {
      'ETH-USDC': 1874.20,
      'ETH-DAI': 1869.50,
      'ETH-WBTC': 0.00053,
      'ETH-SHIB': 5000000,
      'USDC-ETH': 1/1874.20,
      'DAI-ETH': 1/1869.50,
      'WBTC-ETH': 1886.79,
      'SHIB-ETH': 0.0000002,
    };
    const pair = `${fromToken.symbol}-${toToken.symbol}`;
    const rate = conversionRates[pair] || 1;
    if (fromAmount && !isNaN(fromAmount)) {
      const result = parseFloat(fromAmount) * rate;
      setToAmount(result.toFixed(toToken.symbol === 'SHIB' ? 0 : 2));
    }
  }, [fromAmount, fromToken, toToken]);

  // Token selector modal component
  const TokenSelectorModal = ({ onClose, onSelect }) => {
    const tokens = [
      { symbol: 'ETH', name: 'Ethereum', logo: 'E', balance: '1.245' },
      { symbol: 'USDC', name: 'USD Coin', logo: 'U', balance: '1,250.42' },
      { symbol: 'DAI', name: 'Dai Stablecoin', logo: 'D', balance: '845.21' },
      { symbol: 'WBTC', name: 'Wrapped Bitcoin', logo: 'W', balance: '0.024' },
      { symbol: 'SHIB', name: 'Shiba Inu', logo: 'S', balance: '5,450,000' },
    ];
    return (
      <div className="token-modal" onClick={onClose}>
        <div className="modal-content" onClick={e => e.stopPropagation()}>
          <div className="modal-header">
            <h3 className="modal-title">Select a token</h3>
            <button className="close-modal" onClick={onClose}>
              &times;
            </button>
          </div>
          <div className="modal-search">
            <input 
              type="text" 
              className="search-input" 
              placeholder="Search token name or address"
            />
          </div>
          <div className="token-list">
            {tokens.map(token => (
              <div 
                key={token.symbol} 
                className="token-item" 
                onClick={() => onSelect({
                  symbol: token.symbol,
                  name: token.name,
                  logo: token.logo
                })}
              >
                <div className="token-logo">{token.logo}</div>
                <div className="token-info">
                  <div className="token-item-name">{token.name}</div>
                  <div className="token-item-symbol">{token.symbol}</div>
                </div>
                <div className="token-item-balance">{token.balance}</div>
              </div>
            ))}
          </div>
        </div>
      </div>
    );
  };

  // Slippage settings modal component
  const SlippageSettingsModal = ({ onClose, slippage, setSlippage }) => {
    const [customSlippage, setCustomSlippage] = useState(slippage.toString());
    const [selectedOption, setSelectedOption] = useState(null);
    const handleConfirm = () => {
      if (customSlippage && !isNaN(customSlippage)) {
        setSlippage(parseFloat(customSlippage));
      }
      onClose();
    };
    const handleOptionSelect = (value) => {
      setSelectedOption(value);
      setCustomSlippage('');
      setSlippage(parseFloat(value));
    };
    return (
      <div className="slippage-modal">
        <div className="slippage-header">
          <div className="slippage-title">Slippage Settings</div>
          <button className="close-slippage" onClick={onClose}>
            <i className="fas fa-times"></i>
          </button>
        </div>
        <div className="slippage-options">
          {['0.1', '0.5', '1'].map(value => (
            <div 
              key={value}
              className={`slippage-option ${selectedOption === value || (!selectedOption && slippage === parseFloat(value)) ? 'active' : ''}`}
              onClick={() => handleOptionSelect(value)}
            >
              {value}%
            </div>
          ))}
        </div>
        <div className="custom-slippage">
          <input 
            type="number" 
            value={customSlippage}
            onChange={(e) => setCustomSlippage(e.target.value)}
            placeholder="Custom"
            min="0.1"
            max="100"
            step="0.1"
          />
          <span>%</span>
        </div>
        <button className="slippage-confirm-btn" onClick={handleConfirm}>
          Confirm Slippage
        </button>
      </div>
    );
  };

  // Swap logic
  const handleTokenSelect = (token) => {
    if (modalFor === 'from') {
      setFromToken(token);
    } else {
      setToToken(token);
    }
    setShowTokenModal(false);
  };
  const swapTokens = () => {
    const temp = fromToken;
    setFromToken(toToken);
    setToToken(temp);
    const tempAmount = fromAmount;
    setFromAmount(toAmount);
    setToAmount(tempAmount);
  };
  const connectWallet = () => {
    setIsWalletConnected(true);
  };
  const handleSwap = () => {
    if (!isWalletConnected) return;
    const swapBtn = document.getElementById('swapButton');
    const originalText = swapBtn.innerHTML;
    swapBtn.innerHTML = '<i class="fas fa-spinner fa-spin"></i> Processing Swap...';
    swapBtn.disabled = true;
    setTimeout(() => {
      swapBtn.innerHTML = '<i class="fas fa-check"></i> Swap Completed!';
      setTimeout(() => {
        swapBtn.innerHTML = originalText;
        swapBtn.disabled = false;
      }, 2000);
    }, 2000);
  };

  // Tab content components
  const TransactionPage = () => (
    <div className="transactions-container">
      <div className="table-header">
        <div className="table-title">Live Transactions</div>
        <div className="table-filters">
          <button className="filter-btn active">All Trades</button>
          <button className="filter-btn">Buys Only</button>
          <button className="filter-btn">Sells Only</button>
          <button className="action-btn">
            <i className="fas fa-sync-alt"></i>
          </button>
        </div>
      </div>
      <div className="table-container">
        <table className="transactions-table">
          <thead>
            <tr>
              <th>Time</th>
              <th>Type</th>
              <th>Token Amount</th>
              <th>Quote Amount</th>
              <th>Price</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>12:01:15</td>
              <td><span className="type-buy"><i className="fas fa-arrow-up type-icon"></i>Buy</span></td>
              <td>2.15 ETH</td>
              <td>$7,353.54</td>
              <td>$3,420.25</td>
            </tr>
            <tr>
              <td>11:59:02</td>
              <td><span className="type-sell"><i className="fas fa-arrow-down type-icon"></i>Sell</span></td>
              <td>1.00 ETH</td>
              <td>$3,420.25</td>
              <td>$3,420.25</td>
            </tr>
            <tr>
              <td>11:58:10</td>
              <td><span className="type-buy"><i className="fas fa-arrow-up type-icon"></i>Buy</span></td>
              <td>0.50 ETH</td>
              <td>$1,710.13</td>
              <td>$3,420.25</td>
            </tr>
            <tr>
              <td>11:55:44</td>
              <td><span className="type-buy"><i className="fas fa-arrow-up type-icon"></i>Buy</span></td>
              <td>3.00 ETH</td>
              <td>$10,260.75</td>
              <td>$3,420.25</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  );
  const HoldersPage = () => (
    <div className="holders-container">
      <div className="holders-header">
        <div className="holders-title">Top Token Holders</div>
        <a href="#" className="view-all">View All Holders</a>
      </div>
      <div className="holders-grid">
        <div className="holder-card">
          <div className="holder-address">0xA1B2...C3D4</div>
          <div className="holder-amount">3M Base</div>
          <div className="holder-percent">12.5%</div>
          <a className="explorer-btn" href="https://etherscan.io/address/0xA1B2C3D4" target="_blank" rel="noopener noreferrer"><i className="fas fa-external-link-alt"></i></a>
        </div>
        <div className="holder-card">
          <div className="holder-address">0xB2C3...D4E5</div>
          <div className="holder-amount">2.1M Base</div>
          <div className="holder-percent">9.8%</div>
          <a className="explorer-btn" href="https://etherscan.io/address/0xB2C3D4E5" target="_blank" rel="noopener noreferrer"><i className="fas fa-external-link-alt"></i></a>
        </div>
        <div className="holder-card">
          <div className="holder-address">0xC3D4...E5F6</div>
          <div className="holder-amount">1.5M Base</div>
          <div className="holder-percent">8.2%</div>
          <a className="explorer-btn" href="https://etherscan.io/address/0xC3D4E5F6" target="_blank" rel="noopener noreferrer"><i className="fas fa-external-link-alt"></i></a>
        </div>
      </div>
    </div>
  );
  const CommentPage = () => (
    <div className="chat-feed">
      <div className="chat-header">
        <div className="chat-title">
          <i className="fas fa-comment-dots"></i>
          <h2>Token Discussion</h2>
        </div>
        <div className="token-info">
          <div className="token-logo">E</div>
          <div className="token-name">ETH/USDC</div>
        </div>
      </div>
      <div className="chat-messages">
        <div className="message">
          <div className="user-avatar">C</div>
          <div className="message-content">
            <div className="message-header">
              <div className="username">crypto_trader.eth</div>
              <div className="message-time">2 min ago</div>
            </div>
            <div className="message-bubble">
              <div className="message-text">Looks like ETH is testing the support at $3,400. What are your thoughts on this consolidation pattern?</div>
              <div className="reactions">
                <div className="reaction-btn"><span></span><span>12</span></div>
                <div className="reaction-btn"><span></span><span>8</span></div>
                <div className="reaction-btn"><span></span><span>5</span></div>
              </div>
            </div>
            <div className="message-actions">
              <div className="action-btn"><i className="fas fa-reply"></i> Reply</div>
              <div className="action-btn"><i className="fas fa-heart"></i> Like</div>
            </div>
          </div>
        </div>
        <div className="message current-user">
          <div className="user-avatar">Y</div>
          <div className="message-content">
            <div className="message-header">
              <div className="username">you.eth</div>
              <div className="message-time">1 min ago</div>
            </div>
            <div className="message-bubble">
              <div className="message-text">I think we're seeing accumulation here. The RSI is oversold on the 4H chart. Might be a good entry point for a swing trade. What targets are you looking at?</div>
            </div>
            <div className="message-actions">
              <div className="action-btn"><i className="fas fa-reply"></i> Reply</div>
              <div className="action-btn"><i className="fas fa-heart"></i> Like</div>
            </div>
          </div>
        </div>
      </div>
      <div className="chat-input-container">
        <button className="emoji-btn"><i className="far fa-smile"></i></button>
        <textarea className="message-input" placeholder="Type your message..."></textarea>
        <button className="send-btn"><i className="fas fa-paper-plane"></i></button>
      </div>
    </div>
  );

  // Main render
  return (
    <div className="swap-page-v2 mobile-optimized">
      <div className="container">
        {/* Mobile-specific optimizations */}
        {isMobile && (
          <div className="mobile-notice">
            <i className="fas fa-mobile-alt"></i>
            <span>Mobile View - Swipe to navigate tabs</span>
          </div>
        )}
        <TokenHeaderDashboard />
          {/* Chart Panel */}
        <div className="chart-panel">
          <div className="chart-header">
            <div className="chart-title">
                <i className="fas fa-chart-line"></i>
              <h2>ETH/USDC</h2>
              </div>
            <div className="chart-controls">
                {chartTimeframes.map(tf => (
                  <button
                    key={tf.key}
                    className={`chart-btn${chartTimeframe === tf.key ? ' active' : ''}`}
                    onClick={() => setChartTimeframe(tf.key)}
                  >
                    {tf.label}
                  </button>
                ))}
              </div>
            <div className="chart-actions">
                <button className="action-btn" title="Fullscreen"><i className="fas fa-expand"></i></button>
                <button className="action-btn" title="Settings"><i className="fas fa-cog"></i></button>
              </div>
            </div>
          <div className="chart-wrapper" ref={chartContainerRef}></div>
          </div>
          {/* Tabs Dashboard - below chart */}
          <TabsDashboard activeTab={activeTab} onTabChange={setActiveTab} />
        {/* Tab Content */}
        {activeTab === 'swap' && (
          <div className="swap-panel">
            <div className="panel-header">
              <h2 className="panel-title">Swap Tokens</h2>
              <button 
                className="settings-btn" 
                onClick={() => setShowSlippageModal(true)}
              >
                <i className="fas fa-cog"></i>
              </button>
            </div>
            {/* From Token */}
            <div className="token-card">
              <div className="token-header">
                <div className="token-label">From</div>
                <div className="token-balance">Balance: 1.245 {fromToken.symbol}</div>
              </div>
              <div className="token-input-container">
                <input
                  type="number"
                  className="token-amount"
                  value={fromAmount}
                  onChange={(e) => setFromAmount(e.target.value)}
                  min="0"
                />
                <div 
                  className="token-selector" 
                  onClick={() => {
                    setModalFor('from');
                    setShowTokenModal(true);
                  }}
                >
                  <div className="token-logo">{fromToken.logo}</div>
                  <div className="token-name">{fromToken.symbol}</div>
                  <i className="fas fa-chevron-down token-dropdown"></i>
                </div>
              </div>
            </div>
            {/* Swap Direction Toggle */}
            <div className="swap-icon" onClick={swapTokens}>
              <i className="fas fa-arrow-down-arrow-up"></i>
            </div>
            {/* To Token */}
            <div className="token-card">
              <div className="token-header">
                <div className="token-label">To</div>
                <div className="token-balance">Balance: {toToken.symbol === 'SHIB' ? '5,450,000' : '1,250.42'} {toToken.symbol}</div>
              </div>
              <div className="token-input-container">
                <input
                  type="number"
                  className="token-amount"
                  value={toAmount}
                  readOnly
                />
                <div 
                  className="token-selector" 
                  onClick={() => {
                    setModalFor('to');
                    setShowTokenModal(true);
                  }}
                >
                  <div className="token-logo">{toToken.logo}</div>
                  <div className="token-name">{toToken.symbol}</div>
                  <i className="fas fa-chevron-down token-dropdown"></i>
                </div>
              </div>
            </div>
            {/* Price Info */}
            <div className="price-info">
              <div>Price</div>
              <div>1 {fromToken.symbol} = {(parseFloat(toAmount) / parseFloat(fromAmount)).toFixed(toToken.symbol === 'SHIB' ? 0 : 4)} {toToken.symbol}</div>
            </div>
            {/* Swap Button */}
            <button 
              className={`swap-btn ${!isWalletConnected ? 'disabled' : ''}`}
              id="swapButton"
              onClick={isWalletConnected ? handleSwap : connectWallet}
            >
              {!isWalletConnected ? (
                <>
                  <i className="fas fa-wallet"></i>
                  Connect Wallet to Swap
                </>
              ) : (
                <>
                  <i className="fas fa-exchange-alt"></i>
                  Swap Tokens
                </>
              )}
            </button>
          </div>
        )}
        {activeTab === 'transaction' && <TransactionPage />}
        {activeTab === 'holders' && <HoldersPage />}
        {activeTab === 'comment' && <CommentPage />}
      </div>
      {/* Token Selector Modal */}
      {showTokenModal && (
        <TokenSelectorModal 
          onClose={() => setShowTokenModal(false)}
          onSelect={handleTokenSelect}
        />
      )}
      {/* Slippage Settings Modal */}
      {showSlippageModal && (
        <SlippageSettingsModal 
          onClose={() => setShowSlippageModal(false)}
          slippage={slippage}
          setSlippage={setSlippage}
        />
      )}
    </div>
  );
};

export default SwapPageV2;