import React from 'react';
import { NavLink } from 'react-router-dom';

const Footer = () => {
  return (
    <footer>
      <div className="footer-nav">
        <NavLink to="/" className="nav-item" activeClassName="active" exact>
          <i className="fas fa-home nav-icon"></i>
          <span className="nav-label">Home</span>
        </NavLink>
        <NavLink to="/trending" className="nav-item" activeClassName="active">
          <i className="fas fa-fire nav-icon"></i>
          <span className="nav-label">Trending</span>
        </NavLink>
        <NavLink to="/create-token" className="nav-item" activeClassName="active">
          <i className="fas fa-plus-circle nav-icon"></i>
          <span className="nav-label">Create Token</span>
        </NavLink>
        <NavLink to="/manage-tokens" className="nav-item" activeClassName="active">
          <i className="fas fa-cog nav-icon"></i>
          <span className="nav-label">Manage</span>
        </NavLink>
        <NavLink to="/wallet" className="nav-item" activeClassName="active">
          <i className="fas fa-wallet nav-icon"></i>
          <span className="nav-label">Wallet</span>
        </NavLink>
      </div>
    </footer>
  );
};

export default Footer;