import React, { useState, useEffect } from 'react';
import SwapPageV2 from '../../pages/SwapPageV2';
import SwapPageV2PC from '../../pages/SwapPageV2PC';
import Header from './Header';
import Footer from './Footer';

const ResponsiveSwapPage = () => {
  const [isDesktop, setIsDesktop] = useState(false);

  useEffect(() => {
    const checkScreenSize = () => {
      setIsDesktop(window.innerWidth > 1024);
    };

    // Check on mount
    checkScreenSize();

    // Add event listener for window resize
    window.addEventListener('resize', checkScreenSize);

    // Cleanup
    return () => window.removeEventListener('resize', checkScreenSize);
  }, []);

  // Render desktop version for screens larger than 1024px
  if (isDesktop) {
    return (
      <>
        <Header />
        <SwapPageV2PC />
      </>
    );
  }

  // Render mobile/tablet version for screens 1024px and smaller
  return (
    <>
      <Header />
      <SwapPageV2 />
      <Footer />
    </>
  );
};

export default ResponsiveSwapPage; 