import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { useTheme } from '../../context/ThemeContext';

const Header = () => {
  const [walletAddress, setWalletAddress] = useState('');
  const { darkMode, toggleTheme } = useTheme();

  const connectWallet = async () => {
    if (window.ethereum) {
      try {
        const accounts = await window.ethereum.request({
          method: 'eth_requestAccounts'
        });
        setWalletAddress(accounts[0]);
      } catch (error) {
        console.error("Connection error:", error);
      }
    } else {
      alert('Please install MetaMask!');
    }
  };

  return (
    <header>
      <Link to="/" className="logo">
        <i className="fas fa-bolt logo-icon"></i>
        <span>GbBoom</span>
      </Link>
      
      <div className="search-container">
        <i className="fas fa-search search-icon"></i>
        <input type="text" placeholder="Search tokens, contracts, or creators..." />
      </div>
      
      <div className="header-actions">
        <button className="btn btn-secondary" onClick={connectWallet}>
          <i className="fas fa-wallet"></i>
          <span className="btn-text">
            {walletAddress ? `${walletAddress.slice(0,6)}...` : 'Connect Wallet'}
          </span>
        </button>
        <Link to="/create-token" className="btn btn-primary">
          <i className="fas fa-plus"></i>
          <span className="btn-text">Create Token</span>
        </Link>
        
        {/* Theme Toggle inside header */}
        <div className="theme-toggle" onClick={toggleTheme}>
          <i className={darkMode ? "fas fa-sun" : "fas fa-moon"}></i>
        </div>
      </div>
    </header>
  );
};

export default Header;