import React, { useState } from 'react';
import Flatpickr from 'react-flatpickr';
import 'flatpickr/dist/themes/material_blue.css';

const TokenForm = ({ tokenData, handleChange }) => {
  const [launchTime, setLaunchTime] = useState('');
  const [timezone, setTimezone] = useState('local');
  const [launchPreview, setLaunchPreview] = useState('No launch time selected');

  const handleLaunchTimeChange = (selectedDates, dateStr) => {
    if (selectedDates.length > 0) {
      const date = selectedDates[0];
      let formattedDate;
      
      if (timezone === 'utc') {
        formattedDate = date.toUTCString();
      } else {
        formattedDate = date.toLocaleString();
      }
      
      setLaunchTime(dateStr);
      setLaunchPreview(formattedDate);
      handleChange({ target: { name: 'launchTime', value: dateStr } });
    }
  };

  const handleTimezoneChange = (e) => {
    const newTimezone = e.target.value;
    setTimezone(newTimezone);
    
    if (launchTime) {
      const date = new Date(launchTime);
      let formattedDate;
      
      if (newTimezone === 'utc') {
        formattedDate = date.toUTCString();
      } else {
        formattedDate = date.toLocaleString();
      }
      
      setLaunchPreview(formattedDate);
    }
  };

  return (
    <div className="form-content">
      {/* Token Details Section */}
      <div className="form-section">
        <h2 className="section-title">
          <i className="fas fa-coins"></i>
          Token Details
        </h2>
        
        <div className="input-group">
          <div className="form-group">
            <label className="form-label">
              Name
              <span className="required">*</span>
              <span className="info-icon">
                <i className="fas fa-question-circle"></i>
                <span className="tooltip">Token name (max 32 characters)</span>
              </span>
            </label>
            <input 
              type="text" 
              className="form-input" 
              name="name"
              value={tokenData.name}
              onChange={handleChange}
              placeholder="Enter token name"
              maxLength={32}
            />
          </div>
          
          <div className="form-group">
            <label className="form-label">
              Symbol
              <span className="required">*</span>
              <span className="info-icon">
                <i className="fas fa-question-circle"></i>
                <span className="tooltip">Token symbol (3-5 characters)</span>
              </span>
            </label>
            <input 
              type="text" 
              className="form-input" 
              name="symbol"
              value={tokenData.symbol}
              onChange={handleChange}
              placeholder="Enter token symbol (e.g. ETH)"
              maxLength={5}
            />
          </div>
        </div>
        
        <div className="input-group">
          <div className="form-group">
            <label className="form-label">
              Dev Buy (ETH)
              <span className="required">*</span>
              <span className="info-icon">
                <i className="fas fa-question-circle"></i>
                <span className="tooltip">Initial ETH amount to buy at token creation</span>
              </span>
            </label>
            <input 
              type="number" 
              className="form-input" 
              name="devBuy"
              value={tokenData.devBuy}
              onChange={handleChange}
              placeholder="Enter ETH amount"
              step="0.01"
              min="0"
            />
          </div>
          
          <div className="form-group">
            <label className="form-label">
              Migration Threshold (ETH)
              <span className="required">*</span>
              <span className="info-icon">
                <i className="fas fa-question-circle"></i>
                <span className="tooltip">ETH required before migrating to Uniswap V2</span>
              </span>
            </label>
            <input 
              type="number" 
              className="form-input" 
              name="migrationThreshold"
              value={tokenData.migrationThreshold}
              onChange={handleChange}
              placeholder="Enter ETH threshold"
              step="0.01"
              min="0"
            />
          </div>
        </div>
        
        {/* Launch Time Section */}
        <div className="form-group">
          <label className="form-label">
            Launch Time
            <span className="required">*</span>
            <span className="info-icon">
              <i className="fas fa-question-circle"></i>
              <span className="tooltip">Select when token trading should begin</span>
            </span>
          </label>
          
          <div className="datetime-container">
            <div className="datetime-input">
              <Flatpickr
                value={launchTime}
                onChange={handleLaunchTimeChange}
                options={{
                  enableTime: true,
                  dateFormat: "Y-m-d H:i",
                  minDate: "today",
                  defaultDate: new Date(Date.now() + 24 * 60 * 60 * 1000), // Tomorrow
                }}
                placeholder="Select launch date & time"
                className="form-input"
              />
              <i className="fas fa-calendar-alt"></i>
            </div>
            
            <div className="form-group">
              <label className="form-label" style={{ marginBottom: 0 }}>Time Zone</label>
              <select 
                value={timezone} 
                onChange={handleTimezoneChange}
                className="form-input"
              >
                <option value="local">Local Time</option>
                <option value="utc">UTC</option>
              </select>
            </div>
          </div>
          
          <div className="date-preview">
            <i className="fas fa-clock"></i>
            <span>{launchPreview}</span>
          </div>
        </div>
      </div>
      
      {/* Token Socials Section */}
      <div className="form-section">
        <h2 className="section-title">
          <i className="fas fa-link"></i>
          Token Socials (Optional)
        </h2>
        
        <div className="form-group">
          <label className="form-label">
            Description
            <span className="info-icon">
              <i className="fas fa-question-circle"></i>
              <span className="tooltip">Brief description of your token project</span>
            </span>
          </label>
          <textarea 
            className="form-input form-textarea" 
            name="description"
            value={tokenData.description}
            onChange={handleChange}
            placeholder="Describe your token..."
            rows={4}
          />
        </div>
        
        <div className="form-group">
          <label className="form-label">
            Token Image URL
            <span className="info-icon">
              <i className="fas fa-question-circle"></i>
              <span className="tooltip">URL for token logo image (PNG/GIF)</span>
            </span>
          </label>
          <input 
            type="url" 
            className="form-input" 
            name="imageUrl"
            value={tokenData.imageUrl}
            onChange={handleChange}
            placeholder="https://example.com/logo.png"
          />
        </div>
        
        <div className="form-group">
          <label className="form-label">
            Website
            <span className="info-icon">
              <i className="fas fa-question-circle"></i>
              <span className="tooltip">Official project website URL</span>
            </span>
          </label>
          <input 
            type="url" 
            className="form-input" 
            name="website"
            value={tokenData.website}
            onChange={handleChange}
            placeholder="https://example.com"
          />
        </div>
        
        <div className="input-group">
          <div className="form-group">
            <label className="form-label">
              Telegram
              <span className="info-icon">
                <i className="fas fa-question-circle"></i>
                <span className="tooltip">Telegram group or channel URL</span>
              </span>
            </label>
            <input 
              type="url" 
              className="form-input" 
              name="telegram"
              value={tokenData.telegram}
              onChange={handleChange}
              placeholder="https://t.me/example"
            />
          </div>
          
          <div className="form-group">
            <label className="form-label">
              X (Twitter)
              <span className="info-icon">
                <i className="fas fa-question-circle"></i>
                <span className="tooltip">Twitter profile URL</span>
              </span>
            </label>
            <input 
              type="url" 
              className="form-input" 
              name="twitter"
              value={tokenData.twitter}
              onChange={handleChange}
              placeholder="https://twitter.com/example"
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default TokenForm;
