import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import TokenForm from '../components/token/TokenForm';
import TokenPreview from '../components/token/TokenPreview';
import { toast } from 'react-toastify';
import '../styles/token.css';

const CreateTokenPage = () => {
  const navigate = useNavigate();
  const [isCreating, setIsCreating] = useState(false);
  const [tokenData, setTokenData] = useState({
    name: '',
    symbol: '',
    devBuy: '',
    migrationThreshold: '',
    launchTime: '',
    description: '',
    imageUrl: '',
    website: '',
    telegram: '',
    twitter: ''
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setTokenData(prev => ({ ...prev, [name]: value }));
  };

  const validateForm = () => {
    const requiredFields = ['name', 'symbol', 'devBuy', 'migrationThreshold', 'launchTime'];
    const missingFields = requiredFields.filter(field => !tokenData[field]);
    
    if (missingFields.length > 0) {
      toast.error(`Please fill in all required fields: ${missingFields.join(', ')}`);
      return false;
    }

    if (tokenData.name.length > 32) {
      toast.error('Token name must be 32 characters or less');
      return false;
    }

    if (tokenData.symbol.length < 3 || tokenData.symbol.length > 5) {
      toast.error('Token symbol must be between 3-5 characters');
      return false;
    }

    if (parseFloat(tokenData.devBuy) <= 0) {
      toast.error('Dev Buy amount must be greater than 0');
      return false;
    }

    if (parseFloat(tokenData.migrationThreshold) <= 0) {
      toast.error('Migration threshold must be greater than 0');
      return false;
    }

    return true;
  };

  const handleSubmit = async () => {
    if (!validateForm()) {
      return;
    }

    setIsCreating(true);
    
    try {
      // Simulate API call for token creation
      await new Promise(resolve => setTimeout(resolve, 3000));
      
      toast.success('Token created successfully! You will be redirected to the dashboard.');
      
      // Redirect to home page after successful creation
      setTimeout(() => {
        navigate('/');
      }, 2000);
      
    } catch (error) {
      toast.error('Failed to create token. Please try again.');
      setIsCreating(false);
    }
  };

  return (
    <div className="create-token-page">
    <div className="container">
        {/* Form Container */}
      <div className="form-container">
        <div className="form-header">
          <h1 className="form-title">
            <i className="fas fa-plus-circle"></i>
            Create New Token
          </h1>
          <p className="form-subtitle">Fill in the details below to deploy your custom token on the blockchain</p>
        </div>
        
        <TokenForm tokenData={tokenData} handleChange={handleChange} />
        
        <div className="form-footer">
          <button 
            className={`create-btn ${isCreating ? 'creating' : ''}`} 
            onClick={handleSubmit}
            disabled={isCreating}
          >
            {isCreating ? (
              <>
                <i className="fas fa-spinner fa-spin"></i>
                Creating Token...
              </>
            ) : (
              <>
            <i className="fas fa-bolt"></i>
            Create Token
              </>
            )}
          </button>
        </div>
      </div>
      
        {/* Preview Card */}
      <TokenPreview tokenData={tokenData} />
      </div>
    </div>
  );
};

export default CreateTokenPage;