import React, { useRef } from 'react';

const TrendingCarousel = () => {
  const carouselRef = useRef(null);
  
  const trendingTokens = [
    { id: 1, name: 'DogeCoin', symbol: 'DOGE', price: '$0.073', change: '+24.5%', status: 'Migrated' },
    { id: 2, name: 'PepeCoin', symbol: 'PEPE', price: '$0.0000012', change: '+58.3%', status: 'Migrating' },
    { id: 3, name: 'Shiba Inu', symbol: 'SHIB', price: '$0.0000089', change: '-3.2%', status: 'Migrated' },
    { id: 4, name: 'Bitcoin', symbol: 'BTC', price: '$42,560', change: '+1.8%', status: 'Migrated' },
    { id: 5, name: 'Ethereum', symbol: 'ETH', price: '$2,340', change: '+5.2%', status: 'Migrated' }
  ];

  const scrollCarousel = (direction) => {
    if (carouselRef.current) {
      const scrollAmount = direction === 'left' ? -300 : 300;
      carouselRef.current.scrollBy({ left: scrollAmount, behavior: 'smooth' });
    }
  };

  return (
    <div className="trending-carousel">
      <div className="carousel-header">
        <h2 className="section-title">
          <i className="fas fa-fire"></i>
          Trending Tokens
        </h2>
        <div className="carousel-nav">
          <div className="nav-btn" onClick={() => scrollCarousel('left')}>
            <i className="fas fa-chevron-left"></i>
          </div>
          <div className="nav-btn" onClick={() => scrollCarousel('right')}>
            <i className="fas fa-chevron-right"></i>
          </div>
        </div>
      </div>
      
      <div className="trending-tokens" ref={carouselRef}>
        {trendingTokens.map(token => (
          <div className="trending-token" key={token.id}>
            <div className="trending-token-logo">
              {token.name.charAt(0)}
            </div>
            <div className="trending-token-info">
              <div className="trending-token-name">
                {token.name}
                <span className={`migration-status ${token.status === 'Migrated' ? '' : 'migrating'}`}>
                  {token.status}
                </span>
              </div>
              <div className="trending-token-symbol">{token.symbol}</div>
              <div className="trending-token-price">
                {token.price}
                <span className={`price-change ${token.change.includes('+') ? 'positive' : 'negative'}`}>
                  {token.change}
                </span>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default TrendingCarousel;