// Real-time Event Service for WebSocket connections
class EventService {
  constructor() {
    this.ws = null;
    this.reconnectAttempts = 0;
    this.maxReconnectAttempts = 5;
    this.reconnectDelay = 1000;
    this.eventListeners = new Map();
    this.isConnected = false;
    this.connectionUrl = process.env.REACT_APP_WS_URL || 'ws://localhost:5001/events';
  }

  // Connect to WebSocket server
  connect() {
    try {
      this.ws = new WebSocket(this.connectionUrl);
      
      this.ws.onopen = () => {
        console.log('WebSocket connected');
        this.isConnected = true;
        this.reconnectAttempts = 0;
        this.emit('connection', { status: 'connected' });
      };

      this.ws.onmessage = (event) => {
        try {
          const data = JSON.parse(event.data);
          this.handleEvent(data);
        } catch (error) {
          console.error('Failed to parse WebSocket message:', error);
        }
      };

      this.ws.onclose = () => {
        console.log('WebSocket disconnected');
        this.isConnected = false;
        this.emit('connection', { status: 'disconnected' });
        this.attemptReconnect();
      };

      this.ws.onerror = (error) => {
        console.error('WebSocket error:', error);
        this.emit('connection', { status: 'error', error });
      };

    } catch (error) {
      console.error('Failed to create WebSocket connection:', error);
      this.attemptReconnect();
    }
  }

  // Attempt to reconnect
  attemptReconnect() {
    if (this.reconnectAttempts < this.maxReconnectAttempts) {
      this.reconnectAttempts++;
      console.log(`Attempting to reconnect (${this.reconnectAttempts}/${this.maxReconnectAttempts})...`);
      
      setTimeout(() => {
        this.connect();
      }, this.reconnectDelay * this.reconnectAttempts);
    } else {
      console.error('Max reconnection attempts reached');
      this.emit('connection', { status: 'failed' });
    }
  }

  // Handle incoming events
  handleEvent(event) {
    console.log('Received event:', event);
    
    // Emit to specific event listeners
    if (this.eventListeners.has(event.type)) {
      this.eventListeners.get(event.type).forEach(callback => {
        try {
          callback(event.data);
        } catch (error) {
          console.error('Error in event listener:', error);
        }
      });
    }

    // Emit to general event listeners
    this.emit('event', event);
  }

  // Subscribe to specific event types
  subscribe(eventType, callback) {
    if (!this.eventListeners.has(eventType)) {
      this.eventListeners.set(eventType, []);
    }
    this.eventListeners.get(eventType).push(callback);

    // Return unsubscribe function
    return () => {
      const listeners = this.eventListeners.get(eventType);
      if (listeners) {
        const index = listeners.indexOf(callback);
        if (index > -1) {
          listeners.splice(index, 1);
        }
      }
    };
  }

  // Subscribe to token events
  subscribeToTokenEvents(callback) {
    return this.subscribe('TOKEN_CREATED', callback);
  }

  // Subscribe to migration events
  subscribeToMigrationEvents(callback) {
    return this.subscribe('TOKEN_MIGRATED', callback);
  }

  // Subscribe to price updates
  subscribeToPriceUpdates(callback) {
    return this.subscribe('PRICE_UPDATE', callback);
  }

  // Subscribe to swap events
  subscribeToSwapEvents(callback) {
    return this.subscribe('SWAP_EVENT', callback);
  }

  // Subscribe to liquidity events
  subscribeToLiquidityEvents(callback) {
    return this.subscribe('LIQUIDITY_EVENT', callback);
  }

  // Emit events to listeners
  emit(eventType, data) {
    if (this.eventListeners.has(eventType)) {
      this.eventListeners.get(eventType).forEach(callback => {
        try {
          callback(data);
        } catch (error) {
          console.error('Error in event listener:', error);
        }
      });
    }
  }

  // Send message to server
  send(message) {
    if (this.ws && this.isConnected) {
      this.ws.send(JSON.stringify(message));
    } else {
      console.warn('WebSocket not connected, cannot send message');
    }
  }

  // Disconnect
  disconnect() {
    if (this.ws) {
      this.ws.close();
      this.ws = null;
    }
    this.isConnected = false;
    this.eventListeners.clear();
  }

  // Get connection status
  getConnectionStatus() {
    return {
      isConnected: this.isConnected,
      reconnectAttempts: this.reconnectAttempts,
      maxReconnectAttempts: this.maxReconnectAttempts
    };
  }
}

// Create singleton instance
const eventService = new EventService();

export default eventService; 