import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
import '../styles/trending.css';

const TrendingPage = () => {
  const navigate = useNavigate();
  const [trendingTokens, setTrendingTokens] = useState([
    {
      id: 1,
      name: "DogeCoin",
      symbol: "DOGE",
      logo: "D",
      price: "$0.073",
      change: "+24.5%",
      volume: "$1.2B",
      liquidity: "85%",
      migration: "migrated"
    },
    {
      id: 2,
      name: "PepeCoin",
      symbol: "PEPE",
      logo: "P",
      price: "$0.0000012",
      change: "+58.3%",
      volume: "$850M",
      liquidity: "72%",
      migration: "migrating"
    },
    {
      id: 3,
      name: "Shiba Inu",
      symbol: "SHIB",
      logo: "S",
      price: "$0.0000089",
      change: "-3.2%",
      volume: "$620M",
      liquidity: "68%",
      migration: "migrated"
    },
    {
      id: 4,
      name: "Bitcoin",
      symbol: "BTC",
      logo: "B",
      price: "$42,560",
      change: "+1.8%",
      volume: "$24.7B",
      liquidity: "92%",
      migration: "migrated"
    },
    {
      id: 5,
      name: "Ethereum",
      symbol: "ETH",
      logo: "E",
      price: "$2,340",
      change: "+5.2%",
      volume: "$18.3B",
      liquidity: "88%",
      migration: "migrated"
    },
    {
      id: 6,
      name: "CurveCoin",
      symbol: "CRV",
      logo: "C",
      price: "$1.85",
      change: "+12.7%",
      volume: "$320M",
      liquidity: "65%",
      migration: "migrated"
    },
    {
      id: 7,
      name: "Solana",
      symbol: "SOL",
      logo: "S",
      price: "$24.30",
      change: "+8.4%",
      volume: "$4.2B",
      liquidity: "82%",
      migration: "migrated"
    },
    {
      id: 8,
      name: "Chainlink",
      symbol: "LINK",
      logo: "L",
      price: "$7.25",
      change: "+3.1%",
      volume: "$1.1B",
      liquidity: "76%",
      migration: "migrated"
    },
    {
      id: 9,
      name: "ApeCoin",
      symbol: "APE",
      logo: "A",
      price: "$3.85",
      change: "-2.5%",
      volume: "$480M",
      liquidity: "62%",
      migration: "migrated"
    },
    {
      id: 10,
      name: "Polygon",
      symbol: "MATIC",
      logo: "M",
      price: "$0.85",
      change: "+6.9%",
      volume: "$1.5B",
      liquidity: "79%",
      migration: "migrated"
    }
  ]);

  const [stats] = useState([
    {
      title: "24h Volume",
      value: "$128.7B",
      change: "+18.72%",
      icon: "fas fa-chart-line",
      positive: true
    },
    {
      title: "Migrated Tokens",
      value: "42,589",
      change: "+5.3%",
      icon: "fas fa-exchange-alt",
      positive: true
    },
    {
      title: "24h New Tokens",
      value: "1,248",
      change: "+4.81%",
      icon: "fas fa-bolt",
      positive: true
    },
    {
      title: "Upcoming Launches",
      value: "86",
      change: "+12.3%",
      icon: "fas fa-rocket",
      positive: true
    }
  ]);

  // Simulate live updates
  useEffect(() => {
    const interval = setInterval(() => {
      setTrendingTokens(prevTokens => {
        const randomIndex = Math.floor(Math.random() * prevTokens.length);
        const updatedTokens = [...prevTokens];
        const token = updatedTokens[randomIndex];
        
        // Randomly change price and volume
        const priceChange = (Math.random() * 0.2 - 0.1).toFixed(3);
        const volumeChange = (Math.random() * 0.3).toFixed(2);
        
        // Update token data
        const currentPrice = parseFloat(token.price.substring(1).replace(',', ''));
        const newPrice = (currentPrice * (1 + parseFloat(priceChange))).toFixed(5);
        token.price = `$${newPrice}`;
        token.change = `${priceChange > 0 ? '+' : ''}${(priceChange * 100).toFixed(1)}%`;
        
        const currentVolume = parseFloat(token.volume.substring(1, token.volume.length-1).replace(',', ''));
        const newVolume = (currentVolume * (1 + parseFloat(volumeChange))).toFixed(1);
        token.volume = `$${newVolume}${token.volume.slice(-1)}`;
        
        return updatedTokens;
      });
    }, 5000);

    return () => clearInterval(interval);
  }, []);

  const handleViewToken = (token) => {
    toast.info(`Viewing details for ${token.name}`);
    // Navigate to token details page or open modal
  };

  const handleTradeToken = (token) => {
    toast.info(`Initiating trade for ${token.name}`);
    navigate(`/swap/${token.symbol}`);
  };

  const handleTokenClick = (token) => {
    // Navigate to token details or swap page
    navigate(`/swap/${token.symbol}`);
  };

  return (
    <div className="trending-page">
      <div className="container">
        <div className="trending-section">
          {/* Stats Cards */}
          <div className="trending-stats">
            {stats.map((stat, index) => (
              <div key={index} className="stat-card">
                <div className="stat-header">
                  <div className="stat-title">{stat.title}</div>
                  <div className="stat-icon">
                    <i className={stat.icon}></i>
                  </div>
                </div>
                <div className="stat-value">{stat.value}</div>
                <div className={`stat-change ${stat.positive ? 'positive' : 'negative'}`}>
                  <i className={`fas fa-arrow-${stat.positive ? 'up' : 'down'}`}></i>
                  {stat.change}
                </div>
              </div>
            ))}
          </div>
          
          {/* Trending Tokens List */}
          <div className="trending-container">
            <div className="trending-header">
              <div className="header-item">
                <span>Name</span>
                <i className="fas fa-sort"></i>
              </div>
              <div className="header-item" style={{ justifyContent: 'flex-end' }}>
                <span>Price</span>
              </div>
              <div className="header-item" style={{ justifyContent: 'flex-end' }}>
                <span>24h Vol</span>
              </div>
              <div className="header-item" style={{ justifyContent: 'flex-end' }}>
                <span>Liquidity</span>
              </div>
              <div className="header-item" style={{ justifyContent: 'flex-end' }}>
                <span>Actions</span>
              </div>
            </div>
            
            <div className="trending-list">
              {trendingTokens.map((token) => (
                <div 
                  key={token.id} 
                  className={`trending-token ${token.change.startsWith('+') ? 'positive' : 'negative'}`}
                  onClick={() => handleTokenClick(token)}
                >
                  <div className="token-info">
                    <div className="token-logo">{token.logo}</div>
                    <div className="token-details">
                      <div className="token-name">
                        {token.name}
                        <span className={`token-migration ${token.migration}`}>
                          {token.migration === 'migrated' ? 'Migrated' : 'Migrating'}
                        </span>
                      </div>
                      <div className="token-symbol">{token.symbol}</div>
                    </div>
                  </div>
                  <div className="token-price" style={{ justifyContent: 'flex-end', textAlign: 'right' }}>
                    <div className="price-value">{token.price}</div>
                    <div className={`price-change ${token.change.startsWith('+') ? 'positive' : 'negative'}`}>
                      {token.change}
                    </div>
                  </div>
                  <div className="token-volume" style={{ justifyContent: 'flex-end', textAlign: 'right' }}>
                    {token.volume}
                  </div>
                  <div className="token-liquidity" style={{ justifyContent: 'flex-end', textAlign: 'right' }}>
                    <div className="liquidity-value">{token.liquidity}</div>
                    <div className="liquidity-bar">
                      <div 
                        className="liquidity-fill" 
                        style={{ width: token.liquidity }}
                      ></div>
                    </div>
                  </div>
                  <div className="token-actions" style={{ justifyContent: 'flex-end' }}>
                    <button 
                      className="action-btn view-btn"
                      onClick={(e) => {
                        e.stopPropagation();
                        handleViewToken(token);
                      }}
                    >
                      <i className="fas fa-eye"></i> View
                    </button>
                    <button 
                      className="action-btn trade-btn"
                      onClick={(e) => {
                        e.stopPropagation();
                        handleTradeToken(token);
                      }}
                    >
                      <i className="fas fa-exchange-alt"></i> Trade
                    </button>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default TrendingPage; 