import React, { useState, useEffect, useRef } from 'react';
import { useParams, useSearchParams } from 'react-router-dom';
import Footer from '../components/common/Footer';
import '../styles/swapV2PC.css';

const SwapPageV2PC = () => {
  const { tokenSymbol } = useParams();
  const [searchParams] = useSearchParams();
  const [fromToken, setFromToken] = useState({ symbol: 'ETH', name: 'Ethereum', logo: 'E' });
  const [toToken, setToToken] = useState({ 
    symbol: tokenSymbol || 'USDC', 
    name: tokenSymbol ? `${tokenSymbol} Token` : 'USD Coin', 
    logo: tokenSymbol ? tokenSymbol.charAt(0) : 'U' 
  });
  const [fromAmount, setFromAmount] = useState('0.1');
  const [toAmount, setToAmount] = useState(tokenSymbol ? '250.42' : '187.42');
  const [showTokenModal, setShowTokenModal] = useState(false);
  const [modalFor, setModalFor] = useState('from');
  const [slippage, setSlippage] = useState(0.5);
  const [showSlippageModal, setShowSlippageModal] = useState(false);
  const [isWalletConnected, setIsWalletConnected] = useState(false);
  const [chartTimeframe, setChartTimeframe] = useState('1h');
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState('');
  const [transactions, setTransactions] = useState([]);
  const [activeView, setActiveView] = useState('transactions'); // 'transactions' or 'holders'
  const chartContainerRef = useRef(null);
  const chartRef = useRef(null);
  const chatMessagesRef = useRef(null);

  // Check URL parameters for view selection
  useEffect(() => {
    const tabParam = searchParams.get('tab');
    if (tabParam === 'comment') {
      setActiveView('chat');
    } else if (tabParam === 'holders') {
      setActiveView('holders');
    } else {
      setActiveView('transactions');
    }
  }, [searchParams]);

  // Initialize chart
  useEffect(() => {
    let chartInstance = null;
    const initChart = async () => {
      if (chartContainerRef.current) {
        // Always remove any existing chart before creating a new one
        if (chartRef.current) {
          chartRef.current.remove();
          chartRef.current = null;
        }
        const { createChart } = await import('lightweight-charts');
        chartInstance = createChart(chartContainerRef.current, {
          width: chartContainerRef.current.clientWidth,
          height: chartContainerRef.current.clientHeight,
          layout: {
            backgroundColor: 'transparent',
            textColor: getComputedStyle(document.documentElement).getPropertyValue('--text-primary'),
          },
          grid: {
            vertLines: {
              color: getComputedStyle(document.documentElement).getPropertyValue('--card-border'),
            },
            horzLines: {
              color: getComputedStyle(document.documentElement).getPropertyValue('--card-border'),
            },
          },
          timeScale: {
            borderColor: getComputedStyle(document.documentElement).getPropertyValue('--card-border'),
            timeVisible: true,
            secondsVisible: false,
          },
          crosshair: {
            mode: 1,
          },
        });
        chartRef.current = chartInstance;
        const candlestickSeries = chartInstance.addCandlestickSeries({
          upColor: getComputedStyle(document.documentElement).getPropertyValue('--up-color'),
          downColor: getComputedStyle(document.documentElement).getPropertyValue('--down-color'),
          borderDownColor: getComputedStyle(document.documentElement).getPropertyValue('--down-color'),
          borderUpColor: getComputedStyle(document.documentElement).getPropertyValue('--up-color'),
          wickDownColor: getComputedStyle(document.documentElement).getPropertyValue('--down-color'),
          wickUpColor: getComputedStyle(document.documentElement).getPropertyValue('--up-color'),
        });
        // Sample data
        const now = Date.now() / 1000;
        const data = [];
        let open = 3400;
        for (let i = 0; i < 100; i++) {
          const time = now - (100 - i) * 3600;
          const close = open + (Math.random() - 0.5) * 60;
          const high = Math.max(open, close) + Math.random() * 40;
          const low = Math.min(open, close) - Math.random() * 40;
          data.push({ time, open, high, low, close });
          open = close;
        }
        candlestickSeries.setData(data);
        // Add volume indicator
        const volumeSeries = chartInstance.addHistogramSeries({
          color: '#26a69a',
          priceFormat: {
            type: 'volume',
          },
          priceScaleId: '',
          scaleMargins: {
            top: 0.8,
            bottom: 0,
          },
        });
        const volumeData = data.map(d => {
          return {
            time: d.time,
            value: Math.floor((d.high - d.low) * 1000 * Math.random()),
            color: d.close >= d.open ? 
              getComputedStyle(document.documentElement).getPropertyValue('--up-color') : 
              getComputedStyle(document.documentElement).getPropertyValue('--down-color')
          };
        });
        volumeSeries.setData(volumeData);
        // Add moving average line
        const maSeries = chartInstance.addLineSeries({
          color: '#2962FF',
          lineWidth: 2,
        });
        const maData = [];
        const period = 20;
        for (let i = period - 1; i < data.length; i++) {
          let sum = 0;
          for (let j = 0; j < period; j++) {
            sum += data[i - j].close;
          }
          maData.push({
            time: data[i].time,
            value: sum / period
          });
        }
        maSeries.setData(maData);
        // Handle window resize
        const handleResize = () => {
          if (chartInstance && chartContainerRef.current) {
            chartInstance.applyOptions({
              width: chartContainerRef.current.clientWidth,
              height: chartContainerRef.current.clientHeight,
            });
          }
        };
        window.addEventListener('resize', handleResize);
        chartInstance._handleResize = handleResize;
      }
    };
    initChart();
    // Cleanup function
    return () => {
      if (chartRef.current) {
        if (chartRef.current._handleResize) {
          window.removeEventListener('resize', chartRef.current._handleResize);
        }
        chartRef.current.remove();
        chartRef.current = null;
      }
    };
  }, []);

  // Initialize chat messages
  useEffect(() => {
    const initialMessages = [
      {
        user: "C",
        name: "crypto_trader.eth",
        time: "2 min ago",
        text: "ETH is testing the $3,400 resistance. What are your thoughts on this consolidation pattern?",
        reactions: [
          { emoji: "", count: 12 },
          { emoji: "", count: 8 },
          { emoji: "", count: 5 }
        ]
      },
      {
        user: "Y",
        name: "you.eth",
        time: "1 min ago",
        text: "I think we're seeing accumulation here. The RSI is oversold on the 4H chart. Might be a good entry point.",
        reactions: [
          { emoji: "", count: 3 }
        ],
        currentUser: true
      },
      {
        user: "T",
        name: "trading_guru",
        time: "Just now",
        text: "Agreed. I've placed a limit order at $3,380. Targeting $3,500 resistance. Stop at $3,350. Volume is increasing which is bullish. ",
        reactions: [
          { emoji: "", count: 7 },
          { emoji: "", count: 2 }
        ]
      }
    ];
    setMessages(initialMessages);
  }, []);

  // Initialize transactions
  useEffect(() => {
    const generateTransaction = () => {
      const types = ['buy', 'sell'];
      const type = types[Math.floor(Math.random() * types.length)];
      const tokenAmount = (Math.random() * 10).toFixed(4);
      const quoteAmount = (Math.random() * 10000).toFixed(2);
      const price = (3400 + (Math.random() - 0.5) * 100).toFixed(2);
      const addresses = [
        "0x742d35Cc6634C0532925a3b844Bc454e4438f44e",
        "0x8a3c2075Ef5F9C2B2d9b8aD6D9b9B2d9b8aD6D9b2",
        "0xEA674fdDe714fd979de3EdF0F56AA9716B898ec8",
        "0xAb5801a7D398351b8bE11C439e05C5B3259aeC9B"
      ];
      const now = new Date();
      const hours = now.getHours().toString().padStart(2, '0');
      const minutes = now.getMinutes().toString().padStart(2, '0');
      const seconds = now.getSeconds().toString().padStart(2, '0');
      const time = `${hours}:${minutes}:${seconds}`;
      const from = addresses[Math.floor(Math.random() * addresses.length)];
      const to = addresses[Math.floor(Math.random() * addresses.length)];
      return {
        time,
        type,
        tokenAmount,
        quoteAmount,
        price,
        from,
        to
      };
    };

    // Add initial transactions
    const initialTransactions = [];
    for (let i = 0; i < 5; i++) {
      initialTransactions.push(generateTransaction());
    }
    setTransactions(initialTransactions);

    // Simulate live transactions
    const interval = setInterval(() => {
      setTransactions(prev => {
        const newTransaction = generateTransaction();
        const updated = [newTransaction, ...prev.slice(0, 9)]; // Keep only 10 transactions
        return updated;
      });
    }, 3000);

    return () => clearInterval(interval);
  }, []);

  // Conversion rate logic
  useEffect(() => {
    const conversionRates = {
      'ETH-USDC': 3420.25,
      'ETH-DAI': 3415.50,
      'ETH-WBTC': 0.00053,
      'ETH-SHIB': 5000000,
      'USDC-ETH': 1/3420.25,
      'DAI-ETH': 1/3415.50,
      'WBTC-ETH': 1886.79,
      'SHIB-ETH': 0.0000002,
    };
    const pair = `${fromToken.symbol}-${toToken.symbol}`;
    const rate = conversionRates[pair] || 1;
    if (fromAmount && !isNaN(fromAmount)) {
      const result = parseFloat(fromAmount) * rate;
      setToAmount(result.toFixed(toToken.symbol === 'SHIB' ? 0 : 2));
    }
  }, [fromAmount, fromToken, toToken]);

  // Initialize holders data
  const [holders] = useState([
    {
      address: '0xA1B2C3D4E5F6789012345678901234567890ABCD',
      amount: '3M',
      percent: '12.5%'
    },
    {
      address: '0xB2C3D4E5F6789012345678901234567890BCDE',
      amount: '2.1M',
      percent: '9.8%'
    },
    {
      address: '0xC3D4E5F6789012345678901234567890CDEF',
      amount: '1.5M',
      percent: '8.2%'
    },
    {
      address: '0xD4E5F6789012345678901234567890DEF0',
      amount: '1.2M',
      percent: '6.5%'
    },
    {
      address: '0xE5F6789012345678901234567890EF01',
      amount: '950K',
      percent: '5.1%'
    },
    {
      address: '0xF6789012345678901234567890F0123',
      amount: '800K',
      percent: '4.3%'
    }
  ]);

  // Token selector modal component
  const TokenSelectorModal = ({ onClose, onSelect }) => {
    const tokens = [
      { symbol: 'ETH', name: 'Ethereum', logo: 'E', balance: '1.245' },
      { symbol: 'USDC', name: 'USD Coin', logo: 'U', balance: '1,250.42' },
      { symbol: 'DAI', name: 'Dai Stablecoin', logo: 'D', balance: '845.21' },
      { symbol: 'WBTC', name: 'Wrapped Bitcoin', logo: 'W', balance: '0.024' },
      { symbol: 'SHIB', name: 'Shiba Inu', logo: 'S', balance: '5,450,000' },
    ];
    return (
      <div className="token-modal" onClick={onClose}>
        <div className="modal-content" onClick={e => e.stopPropagation()}>
          <div className="modal-header">
            <h3 className="modal-title">Select a token</h3>
            <button className="close-modal" onClick={onClose}>
              &times;
            </button>
          </div>
          <div className="modal-search">
            <input 
              type="text" 
              className="search-input" 
              placeholder="Search token name or address"
            />
          </div>
          <div className="token-list">
            {tokens.map(token => (
              <div 
                key={token.symbol} 
                className="token-item" 
                onClick={() => onSelect({
                  symbol: token.symbol,
                  name: token.name,
                  logo: token.logo
                })}
              >
                <div className="token-logo">{token.logo}</div>
                <div className="token-info">
                  <div className="token-item-name">{token.name}</div>
                  <div className="token-item-symbol">{token.symbol}</div>
                </div>
                <div className="token-item-balance">{token.balance}</div>
              </div>
            ))}
          </div>
        </div>
      </div>
    );
  };

  // Swap logic
  const handleTokenSelect = (token) => {
    if (modalFor === 'from') {
      setFromToken(token);
    } else {
      setToToken(token);
    }
    setShowTokenModal(false);
  };

  const swapTokens = () => {
    const temp = fromToken;
    setFromToken(toToken);
    setToToken(temp);
    const tempAmount = fromAmount;
    setFromAmount(toAmount);
    setToAmount(tempAmount);
  };

  const connectWallet = () => {
    setIsWalletConnected(true);
  };

  const handleSwap = () => {
    if (!isWalletConnected) return;
    const swapBtn = document.getElementById('swapButton');
    const originalText = swapBtn.innerHTML;
    swapBtn.innerHTML = '<i class="fas fa-spinner fa-spin"></i> Processing Swap...';
    swapBtn.disabled = true;
    setTimeout(() => {
      swapBtn.innerHTML = '<i class="fas fa-check"></i> Swap Completed!';
      setTimeout(() => {
        swapBtn.innerHTML = originalText;
        swapBtn.disabled = false;
      }, 2000);
    }, 2000);
  };

  // Chat functionality
  const sendMessage = () => {
    if (newMessage.trim() === '') return;
    
    const now = new Date();
    const timeString = now.toLocaleTimeString([], {hour: '2-digit', minute:'2-digit'});
    
    const userMessage = {
      user: "Y",
      name: "you.eth",
      time: timeString,
      text: newMessage,
      reactions: [],
      currentUser: true
    };
    
    setMessages(prev => [...prev, userMessage]);
    setNewMessage('');
    
    // Auto-reply simulation
    setTimeout(() => {
      const autoReply = {
        user: "B",
        name: "bot_trader",
        time: "Just now",
        text: "Interesting analysis! I'm watching the $3,500 resistance level closely. Volume needs to confirm the breakout.",
        reactions: [
          { emoji: "", count: 1 }
        ]
      };
      setMessages(prev => [...prev, autoReply]);
    }, 2000);
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      sendMessage();
    }
  };

  // Scroll chat to bottom when new messages arrive
  useEffect(() => {
    if (chatMessagesRef.current) {
      chatMessagesRef.current.scrollTop = chatMessagesRef.current.scrollHeight;
    }
  }, [messages]);

  // Holders component
  const HoldersSection = () => (
    <div className="holders-container">
      <div className="holders-header">
        <div className="holders-title">Top Token Holders</div>
        <a href="#" className="view-all">View All Holders</a>
      </div>
      <div className="holders-scroll">
        {holders.map((holder, index) => (
          <div key={index} className="holder-card">
            <div className="holder-address">{holder.address.substring(0, 6)}...{holder.address.substring(38)}</div>
            <div className="holder-amount">{holder.amount} ETH</div>
            <div className="holder-percent">{holder.percent}</div>
            <a 
              className="explorer-btn" 
              href={`https://etherscan.io/address/${holder.address}`} 
              target="_blank" 
              rel="noopener noreferrer"
            >
              <i className="fas fa-external-link-alt"></i>
            </a>
          </div>
        ))}
      </div>
    </div>
  );

  return (
    <div className="desktop-layout">
      <div className="desktop-container">
        {/* Left Panel - Live Chat */}
        <div className="chat-panel">
        <div className="chat-header">
          <div className="chat-title">
            <i className="fas fa-comment-dots"></i>
            <h2>Token Discussion</h2>
          </div>
        </div>
        <div className="chat-messages" ref={chatMessagesRef}>
          {messages.map((message, index) => (
            <div key={index} className={`message ${message.currentUser ? 'current-user' : ''}`}>
              <div className="user-avatar">{message.user}</div>
              <div className="message-content">
                <div className="message-header">
                  <div className="username">{message.name}</div>
                  <div className="message-time">{message.time}</div>
                </div>
                <div className="message-bubble">
                  <div className="message-text">{message.text}</div>
                  {message.reactions && message.reactions.length > 0 && (
                    <div className="reactions">
                      {message.reactions.map((reaction, idx) => (
                        <div key={idx} className="reaction-btn">
                          <span>{reaction.emoji}</span>
                          <span>{reaction.count}</span>
                        </div>
                      ))}
                    </div>
                  )}
                </div>
              </div>
            </div>
          ))}
        </div>
        <div className="chat-input-container">
          <div className="emoji-btn">
            <i className="far fa-smile"></i>
          </div>
          <textarea 
            className="message-input" 
            placeholder="Type your message..."
            value={newMessage}
            onChange={(e) => setNewMessage(e.target.value)}
            onKeyPress={handleKeyPress}
          />
          <button 
            className={`send-btn ${newMessage.trim() === '' ? 'disabled' : ''}`}
            onClick={sendMessage}
          >
            <i className="fas fa-paper-plane"></i>
          </button>
        </div>
      </div>

      {/* Center Panel - Transactions */}
      <div className="center-panel">
        <div className="token-header">
          <div className="token-logo">E</div>
          <div className="token-info">
            <div className="token-name">Ethereum</div>
            <div className="token-symbol">ETH</div>
          </div>
          <div className="token-stats">
            <div className="token-stat">
              <div className="stat-label">Price</div>
              <div className="stat-value">$3,420.25</div>
            </div>
            <div className="token-stat">
              <div className="stat-label">24h Change</div>
              <div className="stat-value price-change positive">+2.75%</div>
            </div>
            <div className="token-stat">
              <div className="stat-label">24h Volume</div>
              <div className="stat-value">$18.2B</div>
            </div>
          </div>
        </div>

        <div className="chart-panel">
          <div className="chart-header">
            <div className="chart-title">
              <i className="fas fa-chart-line"></i>
              <h2>ETH/USDC - 1 Hour Chart</h2>
            </div>
            <div className="chart-controls">
              <button className={`chart-btn ${chartTimeframe === '1h' ? 'active' : ''}`} onClick={() => setChartTimeframe('1h')}>1H</button>
              <button className={`chart-btn ${chartTimeframe === '6h' ? 'active' : ''}`} onClick={() => setChartTimeframe('6h')}>6H</button>
              <button className={`chart-btn ${chartTimeframe === '24h' ? 'active' : ''}`} onClick={() => setChartTimeframe('24h')}>24H</button>
              <button className={`chart-btn ${chartTimeframe === '1w' ? 'active' : ''}`} onClick={() => setChartTimeframe('1w')}>1W</button>
              <button className={`chart-btn ${chartTimeframe === '1m' ? 'active' : ''}`} onClick={() => setChartTimeframe('1m')}>1M</button>
            </div>
          </div>
          <div className="chart-wrapper" ref={chartContainerRef}></div>
        </div>

        <div className="transactions-container">
          <div className="table-header">
            <div className="table-title">
              {activeView === 'transactions' ? 'Live Transactions' : 'Top Holders'}
            </div>
            <div className="table-filters">
              <button 
                className={`filter-btn ${activeView === 'transactions' ? 'active' : ''}`}
                onClick={() => setActiveView('transactions')}
              >
                Live Transaction
              </button>
              <button 
                className={`filter-btn ${activeView === 'holders' ? 'active' : ''}`}
                onClick={() => setActiveView('holders')}
              >
                Holders
              </button>
            </div>
          </div>
          
          {activeView === 'transactions' ? (
          <table className="transactions-table">
            <thead>
              <tr>
                <th>Time</th>
                <th>Type</th>
                <th>Token Amount</th>
                <th>Quote Amount</th>
                <th>Price</th>
                <th>From</th>
                <th>To</th>
              </tr>
            </thead>
            <tbody>
              {transactions.map((transaction, index) => (
                <tr key={index}>
                  <td>{transaction.time}</td>
                  <td className={transaction.type === 'buy' ? 'type-buy' : 'type-sell'}>
                    <i className={`fas fa-arrow-${transaction.type === 'buy' ? 'up' : 'down'} type-icon`}></i>
                    {transaction.type === 'buy' ? 'Buy' : 'Sell'}
                  </td>
                  <td>{transaction.tokenAmount} ETH</td>
                  <td>${transaction.quoteAmount}</td>
                  <td>${transaction.price}</td>
                  <td>{transaction.from.substring(0, 6)}...{transaction.from.substring(38)}</td>
                  <td>{transaction.to.substring(0, 6)}...{transaction.to.substring(38)}</td>
                </tr>
              ))}
            </tbody>
          </table>
          ) : (
            <HoldersSection />
          )}
        </div>
      </div>

      {/* Right Panel - Swap */}
      <div className="swap-panel">
        <div className="panel-header">
          <h2 className="panel-title">Swap Tokens</h2>
          <button className="settings-btn">
            <i className="fas fa-cog"></i>
          </button>
        </div>
        
        {/* From Token */}
        <div className="token-card">
          <div className="token-header">
            <div className="token-label">From</div>
            <div className="token-balance">Balance: 1.245 ETH</div>
          </div>
          <div className="token-input-container">
            <input
              type="number"
              className="token-amount"
              value={fromAmount}
              onChange={(e) => setFromAmount(e.target.value)}
              min="0"
            />
            <div 
              className="token-selector" 
              onClick={() => {
                setModalFor('from');
                setShowTokenModal(true);
              }}
            >
              <div className="token-logo-small">{fromToken.logo}</div>
              <div className="token-name">{fromToken.symbol}</div>
              <i className="fas fa-chevron-down token-dropdown"></i>
            </div>
          </div>
        </div>

        {/* Swap Direction Toggle */}
        <div className="swap-icon" onClick={swapTokens}>
          <i className="fas fa-arrow-down-arrow-up"></i>
        </div>

        {/* To Token */}
        <div className="token-card">
          <div className="token-header">
            <div className="token-label">To</div>
            <div className="token-balance">Balance: 1,250.42 USDC</div>
          </div>
          <div className="token-input-container">
            <input
              type="number"
              className="token-amount"
              value={toAmount}
              readOnly
            />
            <div 
              className="token-selector" 
              onClick={() => {
                setModalFor('to');
                setShowTokenModal(true);
              }}
            >
              <div className="token-logo-small">{toToken.logo}</div>
              <div className="token-name">{toToken.symbol}</div>
              <i className="fas fa-chevron-down token-dropdown"></i>
            </div>
          </div>
        </div>

        {/* Price Info */}
        <div className="price-info">
          <div>Price</div>
          <div>1 {fromToken.symbol} = {(parseFloat(toAmount) / parseFloat(fromAmount)).toFixed(toToken.symbol === 'SHIB' ? 0 : 4)} {toToken.symbol}</div>
        </div>

        {/* Swap Button */}
        <button 
          className={`swap-btn ${!isWalletConnected ? 'disabled' : ''}`}
          id="swapButton"
          onClick={isWalletConnected ? handleSwap : connectWallet}
        >
          {!isWalletConnected ? (
            <>
              <i className="fas fa-wallet"></i>
              Connect Wallet to Swap
            </>
          ) : (
            <>
              <i className="fas fa-exchange-alt"></i>
              Swap Tokens
            </>
          )}
        </button>
      </div>

      {/* Token Selector Modal */}
      {showTokenModal && (
        <TokenSelectorModal 
          onClose={() => setShowTokenModal(false)}
          onSelect={handleTokenSelect}
        />
      )}
      </div>
      <Footer />
    </div>
  );
};

export default SwapPageV2PC;
