import React from 'react';

const TokenPreview = ({ tokenData }) => {
  const formatLaunchTime = (launchTime) => {
    if (!launchTime) return 'Not scheduled';
    try {
      const date = new Date(launchTime);
      return date.toLocaleString();
    } catch (error) {
      return 'Invalid date';
    }
  };

  const hasSocialLinks = tokenData.website || tokenData.telegram || tokenData.twitter;

  return (
    <div className="preview-card">
      <h2 className="preview-title">
        <i className="fas fa-eye"></i>
        Token Preview
      </h2>
      <div className="preview-content">
        <div className="token-preview">
          <div className="preview-header">
            <div className="preview-logo">
              {tokenData.imageUrl ? (
                <img src={tokenData.imageUrl} alt="Token Logo" style={{ width: '100%', height: '100%', borderRadius: '14px' }} />
              ) : (
                <i className="fas fa-coins"></i>
              )}
            </div>
            <div className="preview-info">
              <div className="preview-name">
                {tokenData.name || 'Your Token Name'}
              </div>
              <div className="preview-symbol">
                {tokenData.symbol || 'TKN'}
              </div>
            </div>
          </div>
          
          <div className="preview-stats">
            <div className="preview-stat">
              <div className="preview-label">Dev Buy</div>
              <div className="preview-value">
                {tokenData.devBuy ? `${tokenData.devBuy} ETH` : '0 ETH'}
              </div>
            </div>
            <div className="preview-stat">
              <div className="preview-label">Migration Threshold</div>
              <div className="preview-value">
                {tokenData.migrationThreshold ? `${tokenData.migrationThreshold} ETH` : '0 ETH'}
              </div>
            </div>
            <div className="preview-stat">
              <div className="preview-label">Launch Time</div>
              <div className="preview-value">
                {formatLaunchTime(tokenData.launchTime)}
              </div>
            </div>
          </div>
        </div>
        
        <div className="token-preview">
          <div className="preview-stat">
            <div className="preview-label">Description</div>
            <div className="preview-value">
              {tokenData.description || 'Your token description will appear here once entered.'}
            </div>
          </div>
          <div className="preview-stat">
            <div className="preview-label">Social Links</div>
            <div className="preview-value">
              {hasSocialLinks ? (
                <div className="social-icons" style={{ display: 'flex', gap: '15px', marginTop: '10px' }}>
                  {tokenData.twitter && (
                    <a href={tokenData.twitter} target="_blank" rel="noopener noreferrer" style={{ color: 'var(--accent-blue)' }}>
                      <i className="fab fa-twitter"></i>
                    </a>
                  )}
                  {tokenData.telegram && (
                    <a href={tokenData.telegram} target="_blank" rel="noopener noreferrer" style={{ color: 'var(--accent-blue)' }}>
                      <i className="fab fa-telegram"></i>
                    </a>
                  )}
                  {tokenData.website && (
                    <a href={tokenData.website} target="_blank" rel="noopener noreferrer" style={{ color: 'var(--accent-blue)' }}>
                      <i className="fas fa-globe"></i>
                    </a>
                  )}
                </div>
              ) : (
                <div style={{ display: 'flex', gap: '15px', marginTop: '10px', opacity: 0.5 }}>
                  <i className="fab fa-twitter" style={{ color: 'var(--accent-blue)' }}></i>
                  <i className="fab fa-telegram" style={{ color: 'var(--accent-blue)' }}></i>
                  <i className="fas fa-globe" style={{ color: 'var(--accent-blue)' }}></i>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default TokenPreview;


