import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
import '../styles/wallet.css';

const WalletPage = () => {
  const navigate = useNavigate();
  const [isDisconnecting, setIsDisconnecting] = useState(false);
  const [isCopied, setIsCopied] = useState(false);
  const [walletTokens, setWalletTokens] = useState([
    {
      id: 1,
      name: "Ethereum",
      symbol: "ETH",
      logo: "https://placehold.co/40x40/2563eb/ffffff/png?text=ETH",
      balance: "3.456",
      value: "$12,123.45",
      change: "+2.35%"
    },
    {
      id: 2,
      name: "USDC",
      symbol: "USDC",
      logo: "https://placehold.co/40x40/2563eb/ffffff/png?text=USDC",
      balance: "1,250.42",
      value: "$1,250.42",
      change: "0.00%"
    },
    {
      id: 3,
      name: "DogeCoin",
      symbol: "DOGE",
      logo: "https://placehold.co/40x40/2563eb/ffffff/png?text=DOGE",
      balance: "12,450.25",
      value: "$912.50",
      change: "+24.5%"
    },
    {
      id: 4,
      name: "Uniswap",
      symbol: "UNI",
      logo: "https://placehold.co/40x40/2563eb/ffffff/png?text=UNI",
      balance: "45.25",
      value: "$320.75",
      change: "-3.2%"
    },
    {
      id: 5,
      name: "Chainlink",
      symbol: "LINK",
      logo: "https://placehold.co/40x40/2563eb/ffffff/png?text=LINK",
      balance: "120.50",
      value: "$725.25",
      change: "+5.2%"
    },
    {
      id: 6,
      name: "Shiba Inu",
      symbol: "SHIB",
      logo: "https://placehold.co/40x40/2563eb/ffffff/png?text=SHIB",
      balance: "24,560,300",
      value: "$185.25",
      change: "-2.5%"
    }
  ]);

  const [transactions] = useState([
    {
      id: 1,
      name: "Swapped ETH for USDC",
      icon: "exchange-alt",
      time: "2 min ago",
      value: "$1,250.42",
      status: "completed"
    },
    {
      id: 2,
      name: "Swapped USDC for ETH",
      icon: "exchange-alt",
      time: "15 min ago",
      value: "0.5 ETH",
      status: "completed"
    },
    {
      id: 3,
      name: "Swapped ETH for DOGE",
      icon: "exchange-alt",
      time: "45 min ago",
      value: "1,000 DOGE",
      status: "completed"
    },
    {
      id: 4,
      name: "Swapped UNI for ETH",
      icon: "exchange-alt",
      time: "1 hour ago",
      value: "45.25 UNI",
      status: "completed"
    },
    {
      id: 5,
      name: "Swapped ETH for SHIB",
      icon: "exchange-alt",
      time: "3 hours ago",
      value: "10,000 SHIB",
      status: "completed"
    }
  ]);

  // Simulate live updates for token values
  useEffect(() => {
    const interval = setInterval(() => {
      setWalletTokens(prevTokens => {
        const randomIndex = Math.floor(Math.random() * prevTokens.length);
        const updatedTokens = [...prevTokens];
        const token = updatedTokens[randomIndex];
        
        // Randomly change price
        const priceChange = (Math.random() * 0.2 - 0.1).toFixed(3);
        const currentValue = parseFloat(token.value.substring(1).replace(',', ''));
        const newValue = currentValue * (1 + parseFloat(priceChange));
        
        // Update token data
        token.value = `$${newValue.toFixed(2)}`;
        token.change = `${priceChange > 0 ? '+' : ''}${(priceChange * 100).toFixed(2)}%`;
        
        return updatedTokens;
      });
    }, 10000);

    return () => clearInterval(interval);
  }, []);

  const handleCopyAddress = async () => {
    try {
      await navigator.clipboard.writeText('0x742d35Cc6634C0532925a3b844Bc454e4438f44e');
      setIsCopied(true);
      toast.success('Wallet address copied to clipboard!');
      setTimeout(() => setIsCopied(false), 2000);
    } catch (error) {
      toast.error('Failed to copy address');
    }
  };

  const handleDisconnectWallet = async () => {
    setIsDisconnecting(true);
    try {
      // Simulate disconnection process
      await new Promise(resolve => setTimeout(resolve, 1500));
      toast.success('Wallet disconnected successfully!');
      navigate('/');
    } catch (error) {
      toast.error('Failed to disconnect wallet');
    } finally {
      setIsDisconnecting(false);
    }
  };

  const handleViewChart = (token) => {
    toast.info(`Viewing chart for ${token.name}`);
    // Navigate to chart page or open modal
  };

  const handleViewAllTransactions = () => {
    toast.info('Viewing all transactions');
    // Navigate to transactions page or open modal
  };

  const handleTokenClick = (token) => {
    // Navigate to token details or swap page
    navigate(`/swap/${token.symbol}`);
  };

  return (
    <div className="wallet-page">
      <div className="container">
        <div className="wallet-section">
          {/* Wallet Summary */}
          <div className="wallet-summary">
            <div className="total-balance">
              <div className="balance-label">Total Balance</div>
              <div className="balance-value">$12,456.78</div>
            </div>
            
            <div className="wallet-address">
              <div className="address-text">0x742d...f44e</div>
              <button 
                className="copy-btn" 
                onClick={handleCopyAddress}
                disabled={isCopied}
              >
                <i className={isCopied ? "fas fa-check" : "fas fa-copy"}></i>
              </button>
            </div>
            
            <div className="network-info">
              <div className="network-dot"></div>
              <div className="network-name">Ethereum Mainnet</div>
            </div>
            
            <button 
              className="disconnect-btn" 
              onClick={handleDisconnectWallet}
              disabled={isDisconnecting}
            >
              {isDisconnecting ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> Disconnecting...
                </>
              ) : (
                <>
                  <i className="fas fa-plug-circle-xmark"></i> Disconnect Wallet
                </>
              )}
            </button>
          </div>
          
          {/* Token Balances */}
          <div className="tokens-container">
            <div className="tokens-header">
              <div className="tokens-title">Token Balances</div>
            </div>
            
            <div className="token-header">
              <div className="header-item">
                <span>Name</span>
                <i className="fas fa-sort"></i>
              </div>
              <div className="header-item" style={{ justifyContent: 'flex-end' }}>
                <span>Balance</span>
              </div>
              <div className="header-item" style={{ justifyContent: 'flex-end' }}>
                <span>Value</span>
              </div>
              <div className="header-item" style={{ justifyContent: 'flex-end' }}>
                <span>Actions</span>
              </div>
            </div>
            
            <div className="tokens-list">
              {walletTokens.map((token) => (
                <div 
                  key={token.id} 
                  className={`token-item ${token.change.startsWith('+') ? 'positive' : 'negative'}`}
                  onClick={() => handleTokenClick(token)}
                >
                  <div className="token-info">
                    <div className="token-logo">
                      <img src={token.logo} alt={token.name} />
                    </div>
                    <div className="token-details">
                      <div className="token-name">
                        {token.name}
                        <span className="token-symbol">{token.symbol}</span>
                      </div>
                    </div>
                  </div>
                  <div className="token-balance">
                    <div className="balance-amount">{token.balance}</div>
                    <div className="balance-usd">{token.symbol}</div>
                  </div>
                  <div className="token-value">
                    <div className="value-amount">{token.value}</div>
                    <div className={`value-change ${token.change.startsWith('+') ? 'positive' : 'negative'}`}>
                      {token.change}
                    </div>
                  </div>
                  <div className="token-actions">
                    <button 
                      className="action-btn view-btn"
                      onClick={(e) => {
                        e.stopPropagation();
                        handleViewChart(token);
                      }}
                    >
                      <i className="fas fa-chart-line"></i> Chart
                    </button>
                  </div>
                </div>
              ))}
            </div>
          </div>
          
          {/* Recent Transactions */}
          <div className="transactions-section">
            <div className="transactions-header">
              <div className="transactions-title">Recent Transactions</div>
              <button 
                className="view-all-btn"
                onClick={handleViewAllTransactions}
              >
                View All
              </button>
            </div>
            <div className="transactions-list">
              {transactions.map((transaction) => (
                <div key={transaction.id} className="transaction-item">
                  <div className="transaction-info">
                    <div className="transaction-icon">
                      <i className={`fas fa-${transaction.icon}`}></i>
                    </div>
                    <div className="transaction-details">
                      <div className="transaction-name">{transaction.name}</div>
                      <div className="transaction-time">{transaction.time}</div>
                    </div>
                  </div>
                  <div className="transaction-value">
                    <div className="value-amount">{transaction.value}</div>
                    <div className={`value-status ${transaction.status}`}>
                      Completed
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default WalletPage; 